import React, { useState } from 'react';
import OrderScaleShape from "../OrderScaleShape"

export default class OrderBox extends React.Component {
  constructor(props) {
    super(props);
    this.state = {scaleData: [[1,10], [2, 15], [3, 20], [4, 30], [5, 40], [6, 60], [7, 80]]};
  }

  // scale scaleData y coordinates find percentage
  normalizeData() {
    const sum = this.state.scaleData.reduce((pv, cv) => pv + cv[1], 0)
    return this.state.scaleData.map(coord => [coord[0], coord[1]/sum])
  }

  render() {
    return (
      <div>
        {/* tabs */}
        <nav className="bg-white px-8 pt-2 shadow-md mb-6">
           <div className="-mb-px flex justify-center">
               <a className="no-underline text-gray-300 border-b-2 border-transparent uppercase tracking-wide font-medium text-xs py-3 mr-8" href="#">
                   Overview
               </a>
               <a className="no-underline text-indigo-600 border-b-2 border-teal-400 uppercase tracking-wide font-medium text-xs py-3 mr-8" href="#">
                   Entry
               </a>
               <a className="no-underline text-gray-300 border-b-2 border-transparent uppercase tracking-wide font-medium text-xs py-3 mr-8" href="#">
                  <span className="hidden sm:block">Take Profit</span>
                  <span className="sm:hidden">TP</span>
               </a>
               <a className="no-underline text-gray-300 border-b-2 border-transparent uppercase tracking-wide font-medium text-xs py-3" href="#">
                 <span className="hidden sm:block">Stop Loss</span>
                 <span className="sm:hidden">SL</span>
               </a>
           </div>
        </nav>
        {/* advanced order controls */}
        <div className="flex items-center">
          <div className="flex-initial mx-4 uppercase text-sm">
            <ul className="font-medium text-gray-500 tracking-wider">
              <li className="px-2 py-2">Single</li>
              <li className="px-2 py-2">Multiple</li>
              <li className="px-2 py-2 underline text-gray-800">Scaled</li>
              <li className="px-2 py-2">{"Function"}</li>
            </ul>
          </div>
          <div className="flex-grow">
            <OrderScaleShape
              className="fill-current text-teal-500 mx-5 md:mx-10"
              data={this.state.scaleData}
              normalizedData={this.normalizeData()}
              width={150}
              height={100}
              resolution={[8, 100]}
              setCoords={(x, y) => {
                let scaleData = this.state.scaleData
                scaleData[x][1] = Math.max(0, y)
                this.setState({scaleData: scaleData})
              }} />
          </div>
        </div>
      </div>
    )
  }
}
